(function($) {
    'use strict';

    var templatename = {

        init: function() {
            this.pageLoader();
            // call below defined function on init
            this.menu();
            this.themeChooser();

            if(jQuery('.svgImage').length) {
                this.svgToinlineSvg();
            }


        },
        pageLoader: function () {
            let perfData = window.performance.timing,
              EstimatedTime = -(perfData.loadEventEnd - perfData.navigationStart),
              time = parseInt((EstimatedTime/1000)%60)*100,
              PercentageID = jQuery("#odometerNumber"),
              start = 0,
              end = 100,
              range,
              stepTime;

            // Loadbar Animation
            setTimeout(function(){
                jQuery('#pageloader').addClass('page-loaded');
            }, time);

            range = end - start,
              stepTime = Math.abs(Math.floor(time / range));

            setInterval(function() {
                PercentageID.html(100);
            }, stepTime);
        },

        menu : function () {

            // Hide Header on on scroll down
            let didScroll,
              lastScrollTop = 5,
              delta = 5,
              element = jQuery('header'),
              navbarHeight = 100,
              st;

            $(window).scroll(function (event) {

                st = $(this).scrollTop();

                if (st > 0) {
                    if (st > lastScrollTop) {
                        // downscroll code
                        element.removeClass('nav-down');
                        element.removeClass('nav-sticky');
                        element.addClass('nav-up')
                    } else {
                        // uproll code
                        element.removeClass('nav-down');
                        element.removeClass('nav-up');
                        element.addClass('nav-sticky')
                    }
                } else {
                    element.removeClass('nav-up');
                    element.removeClass('nav-sticky');
                    element.addClass('nav-down')
                }
                lastScrollTop = st;
            });

            // menu toggle
            jQuery('.menu-toggle').on('click', function() {
                jQuery('.hamburger-menu').toggleClass('animate');
                jQuery('body').toggleClass('menuOpen');
            });
        },

        themeChooser: function() {
            jQuery('#darkMode').change(function() {
                if(this.checked) {
                    jQuery('html').addClass('theme-dark');
                    localStorage.setItem('themeColor', 'theme-dark');
                }else {
                    jQuery('html').removeClass('theme-dark');
                    localStorage.setItem('themeColor', '');
                }

            });

            let themeName = localStorage.getItem('themeColor');

            if(themeName === undefined || themeName === null || themeName === '') {
                jQuery('html').removeClass('theme-dark');
                localStorage.setItem('themeColor', '');
            }else {
                jQuery('html').addClass(themeName);
                if(themeName === 'theme-dark') {
                    jQuery('#darkMode').prop('checked', true);
                }else {
                    jQuery('#darkMode').prop('checked', false);
                }
            }
        },

        svgToinlineSvg: function () {
            jQuery('img.svgImage').filter(function() {
                return this.src.match(/.*\.svg$/);
            }).each(function(){
                var $img = jQuery(this);
                var imgID = $img.attr('id');
                var imgClass = $img.attr('class');
                var imgURL = $img.attr('src');

                jQuery.get(imgURL, function(data) {
                    // Get the SVG tag, ignore the rest
                    var $svg = jQuery(data).find('svg');

                    // Add replaced image's ID to the new SVG
                    if(typeof imgID !== 'undefined') {
                        $svg = $svg.attr('id', imgID);
                    }
                    // Add replaced image's classes to the new SVG
                    if(typeof imgClass !== 'undefined') {
                        $svg = $svg.attr('class', imgClass+' replaced-svg');
                    }

                    // Remove any invalid XML tags as per http://validator.w3.org
                    $svg = $svg.removeAttr('xmlns:a');

                    // Replace image with new SVG
                    $img.replaceWith($svg);

                }, 'xml');
            });
        },
    };

    templatename.init();

})(jQuery);